@keyframes preloader-inside-white {
    0% {
        -webkit-transform: scale(0, 0);
        -moz-transform: scale(0, 0);
        -ms-transform: scale(0, 0);
        -o-transform: scale(0, 0);
        transform: scale(0, 0);
    }

    100% {
        -webkit-transform: scale(1, 1);
        -moz-transform: scale(1, 1);
        -ms-transform: scale(1, 1);
        -o-transform: scale(1, 1);
        transform: scale(1, 1);
    }
}

@keyframes preloader-inside-red {
    0% {
        -webkit-transform: scale(0, 0);
        -moz-transform: scale(0, 0);
        -ms-transform: scale(0, 0);
        -o-transform: scale(0, 0);
        transform: scale(0, 0);
    }

    30% {
        -webkit-transform: scale(0, 0);
        -moz-transform: scale(0, 0);
        -ms-transform: scale(0, 0);
        -o-transform: scale(0, 0);
        transform: scale(0, 0);
    }

    100% {
        -webkit-transform: scale(1, 1);
        -moz-transform: scale(1, 1);
        -ms-transform: scale(1, 1);
        -o-transform: scale(1, 1);
        transform: scale(1, 1);
    }
}

.preloader {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    z-index: 10;
    padding: 20px;
}

.preloader_type_button {
    padding: 0;
}

.preloader__container {
    width: 100px;
    height: 100px;
    position: relative;
}

.preloader__round {
    width: 100px;
    height: 100px;
    position: absolute;
    top: 0;
    left: 0;
    background: #FF6838;
    border-radius: 50px;
}

.preloader__round::after,
.preloader__round::before {
    content: '';
    width: 100px;
    height: 100px;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50px;
}

.preloader__round::after {
    background: grey;
    animation: preloader-inside-white 1s ease-in-out infinite;
}

.preloader__round::before {
    z-index: 10;
    background: #FF6838;
    animation: preloader-inside-red 1s ease-in-out infinite;
}

.preloader__container_type_button {
    width: 40px;
    height: 40px;
  }
  
  .preloader__container_type_button-small {
    width: 16px;
    height: 16px;
  }
  
  .preloader__round_type_button {
    background: #4285F4;
    width: 40px;
    height: 40px;
  }
  
  .preloader__round_type_button::after {
    width: 40px;
    height: 40px;
    background: lightgray;
  }
  
  .preloader__round_type_button::before {
    width: 40px;
    height: 40px;
    background: #4285F4;
  }
  
  .preloader__round_type_button-small {
    background: var(--dark-color);
    width: 16px;
    height: 16px;
  }
  
  .preloader__round_type_button-small::after,
  .preloader__round_type_button-small::before {
    width: 16px;
    height: 16px;
  }