/* inter-regular - cyrillic_latin */
@font-face {
  font-display: swap;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  src: url('inter-v12-cyrillic_latin-regular.eot');
  src: url('inter-v12-cyrillic_latin-regular.eot?#iefix') format('embedded-opentype'),
       url('inter-v12-cyrillic_latin-regular.woff2') format('woff2'),
       url('inter-v12-cyrillic_latin-regular.woff') format('woff'),
       url('inter-v12-cyrillic_latin-regular.ttf') format('truetype'),
       url('inter-v12-cyrillic_latin-regular.svg#Inter') format('svg');
}

