.search__page {
  padding: 70px 70px 0;
}

@media screen and (max-width:1020px) {
  .search__page {
      padding-top: 80px;
      padding-left: 30px;
      padding-right: 30px;
  }
}

@media screen and (max-width:760px) {
  .search__page {
      padding-top: 78px;
      padding-left: 14px;
      padding-right: 14px;
  }
}

.search__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}

@media screen and (max-width: 520px) {
  .search__container {
      padding-bottom: 39px;
  }
}

.search__form {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 16px;
  padding-bottom: 11px;
  box-sizing: border-box;
  border-bottom: 1.5px solid #424242;
}

@media screen and (max-width: 760px) {
  .search__form {
      margin-right: 0px;
      margin-bottom: 13px;
  }
}

@media screen and (max-width: 520px) {
  .search__form {
      margin-bottom: 21px;
      padding-top: 6px;
      padding-bottom: 9px;
  }
}

.search__input {
  background-color: transparent;
  border: none;
  width: 90%;
  font-size: 28px;
  font-weight: 500;
  line-height: normal;
  color: var(--secondary-font-color);
}

@media screen and (max-width: 520px) {
  .search__input {
      font-size: 18px;
      font-weight: 500;
      line-height: normal;
  }
}

.search__input:focus {
  outline: 0.2px solid #4285F4;
}

.search__submit {
  width: 34px;
  height: 34px;
  padding: 0;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  background-image: url('../../../images/search.svg');
  background-repeat: no-repeat;
  cursor: pointer;
  transition: opacity .3s linear;
}

@media (hover: hover) and (pointer: fine) {
  .search_submit:hover {
      opacity: .7;
  }
}

.search__error {
  visibility: hidden;
  min-height: 14px;
  text-align: center;
  font-size: 11px;
  font-weight: 400;
  line-height: normal;
  color: #FF004C;
}

.search__error_active {
  visibility: visible;
}

@media screen and (max-width: 520px) {
  .search__error {
      margin-bottom: 3px;
  }
}

.search__container-label {
  display: flex;
  align-self: flex-start;
  cursor: pointer;
  transition: opacity .3s linear;
  padding-bottom: 46px;
}

@media screen and (max-width: 760px) {
  .search__container-label {
    align-self: flex-end;
    flex-direction: row-reverse;
  }
}

@media screen and (max-width: 525px) {
  .search__container-label {
    align-self: center;
  }
}

.search__container-label_disabled {
  pointer-events: none;
}