.error {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .error__container {
    padding-top: 246px;
  }
  
  @media screen and (max-width: 760px) {
    .error__container {
      padding-top: 407px;
    }
  }
  
  @media screen and (max-width: 520px) {
    .error__container {
      padding-top: 329px;
    }
  }
  
  
  .error__title {
    margin: 0 0 5px 0;
    text-align: center;
    font-size: 140px;
    font-weight: 400;
    line-height: normal;
    color: var(--main-font-color);
  }
  
  @media screen and (max-width: 520px) {
    .error__title {
      font-size: 80px;
      margin-bottom: 10px;
    }
  }
  
  .error__text {
    margin: 0 0 184px 0;
    text-align: center;
    font-size: 16px;
    font-weight: 400;
    line-height: normal;
    color: var(--main-font-color);
  }
  
  @media screen and (max-width: 520px) {
    .error__text {
      font-size: 12px;
      margin-bottom: 285px;
    }
  }
  
  .error__link {
    display: block;
    text-align: center;
    text-decoration: none;
    color: #4285F4;
    transition: opacity .3s linear;
  }
  
  @media screen and (max-width: 520px) {
    .error__link {
      font-size: 12px;
    }
  }
  
  @media (hover: hover) and (pointer: fine) {
    .error__link:hover {
      opacity: .7;
      cursor: pointer;
    }
  }